import React,{useContext} from "react";
import logoMobile from "../../Assets/Images/logoDB_mobile.svg";
import logoDesktop from "../../Assets/Images/logoDB_desktop.svg";
import {
  HeaderDB,
  MobileLogo,
  DesktopLogo,
  Button,
  ButtonContanier,
  LogoContainer,
} from "./Header.styles";
import MobileMenu from "../MobileMenu/MobileMenu";
import { useNavigate } from "react-router-dom";
import { User, Card, Circle } from "./headerStyle.js";
import { DataContext } from "../../Context/DataProvider";
import { types } from "../../Context/dataReducer";

const Header = () => {
  const navigate = useNavigate();
  const pathName = window.location.pathname;

  const clear = () => {
    sessionStorage.clear();
    window.location.reload();
  };

  const [state ,dispatch] = useContext(DataContext)
  const {userData} = state
  
  console.log(userData);

  const dataProductHandler=()=>{
    dispatch({
      type: types.allProducts
    })
    sessionStorage.removeItem("checkIn")
    sessionStorage.removeItem("checkOut")
  }

  return (
    <>
        <HeaderDB>
          <MobileMenu />
          <LogoContainer>
            <MobileLogo
              src={logoMobile}
              alt="Logo Digital Booking"
              onClick={() => {navigate("/");dataProductHandler()}}
              ></MobileLogo>
            <DesktopLogo
              src={logoDesktop}
              alt="Logo Digital Booking"
              onClick={() => {navigate("/");dataProductHandler()}}
              ></DesktopLogo>
          </LogoContainer>
          {sessionStorage.getItem("user") ? (
          
            <Card>
              
              <Circle>
                {userData?.name.charAt(0)}
                {userData?.surname.charAt(0)}
               
              </Circle>
              <User>
                <h4>Hola, </h4>
                <p>
                  {userData?.name} {userData?.surname}
                </p>
              </User>
              <button onClick={clear}>X</button>
            </Card>
          
              ) : (
            <ButtonContanier>
              {pathName!== "/register" && <Button onClick={() => navigate("/register")}>Crear cuenta </Button>}
              {pathName!== "/login" &&<Button onClick={() => navigate("/login")}>Iniciar sesin</Button>}
            </ButtonContanier> )}
        </HeaderDB>
    </>
  );
};

export default Header;
